.burger {
  width: 50px;
  height: 50px;
  background: none;
  appearance: none;
  border: none;
  display: block;
  transition: transform .25s ease-in-out;
}

.burger__in {
  display: block;
  margin: 0 auto;
  width: 30px;
  height: 22px;
  position: relative;
}

.burger__in > span {
  display: block;
  width: 100%;
  height: 2px;
  background: grey;
  position: absolute;
  top: 10px;
  left: 0;
}

.burger__in > span:nth-child(1), .burger__in > span:nth-child(3) {
  transition: top .15s ease-in .15s, transform .25s ease-in-out;
}

.burger__in > span:nth-child(1) {
  top: 0;
}

.burger__in > span:nth-child(3) {
  top: 20px;
}

.burger__in > span:nth-child(2) {
  transition: opacity 0s linear .15s;
}

.burger.is-active .burger__in > span:nth-child(1), .burger.is-active .burger__in > span:nth-child(3) {
  top: 10px;
  transition: top .15s ease-in, transform .25s ease-in-out .15s;
}

.burger.is-active .burger__in > span:nth-child(2) {
  opacity: 0;
}

.burger.is-active .burger__in > span:nth-child(1) {
  transform: rotate(45deg);
}

.burger.is-active .burger__in > span:nth-child(3) {
  transform: rotate(-45deg);
}

.block {
  display: flex;
}
