@include font('HelveticaNeueCyr-Bold', 'HelveticaNeueCyr-Bold');
@include font('HelveticaNeueCyr-Light', 'HelveticaNeueCyr-Light');
@include font('HelveticaNeueCyr-Medium', 'HelveticaNeueCyr-Medium');
@include font('HelveticaNeueCyr-Roman', 'HelveticaNeueCyr-Roman');
@include font('HelveticaNeueCyr-Thin', 'HelveticaNeueCyr-Thin');
@mixin HB {
	font-family: 'HelveticaNeueCyr-Bold' ,sans-serif; }
@mixin HL {
	font-family: 'HelveticaNeueCyr-Light',sans-serif;
	font-weight: normal; }
@mixin HM {
	font-family: 'HelveticaNeueCyr-Medium',sans-serif; }
@mixin HR {
	font-family: 'HelveticaNeueCyr-Roman',sans-serif; }
@mixin HT {
	font-family: 'HelveticaNeueCyr-Thin',sans-serif; }
